import React, { useEffect, useState } from "react";
import axios from "axios";
import NasaComponent from "./NasaComponent";
// https://api.nasa.gov/planetary/apod?start_date=2021-12-06&api_key=DEMO_KEY

const Nasa = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const Alldata = await axios.get(
          "https://api.nasa.gov/planetary/apod?start_date=2021-12-06&api_key=p9aQKETG6wAj7SvyXqySy0k2lCMHStR1dZrdTAt9"
        );
        setData(Alldata.data);
      } catch (err) {
        setError(err);
      }
      setLoading(false);
    }
    fetchData();
  }, []);

  return loading === true ? (
    <div className="lds-roller">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  ) : (
    <div
      style={{
        display: "flex",
        marginTop: "20px",
        marginBottom: "20px",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "space-evenly",
      }}
    >
      {data.map((dataEle, index) => {
        return <NasaComponent key={index} data={dataEle} />;
      })}
    </div>
  );
};

export default Nasa;
